import { useContext, useEffect, useState } from "react";
import {
  Breadcrumb,
  Button,
  Form,
  Typography,
  Table,
  Modal,
  Space,
  Tag,
  Image,
  message,
  Select,
} from "antd";
import {
  Card,
  FlexContainer,
  FlexEnd,
  FormRangePicker,
  FormSelect,
} from "../../Components";
import withAuth from "../../HOC/withAuth";
import { useDisclosure, useGetData, useGetDataPagination } from "../../Hooks";
import { AuthContext } from "../../Store/AuthContext";
import { FiFilter } from "react-icons/fi";
import { RiFileExcel2Line } from "react-icons/ri";
import moment from "moment";
import { successColor, dangerColor } from "../../Utils/colors";
import fileDownload from "js-file-download";
import { endpoint } from "../../Utils/enpoint";
import axios from "axios";

const { Title, Text } = Typography;
const { Option } = Select;

const ListContent = ({ label, children }) => (
  <div
    style={{ display: "flex", flexDirection: "column", marginBottom: "14px" }}
  >
    <Text type="secondary">{label}</Text>
    <Title level={5} style={{ marginTop: "0px" }}>
      {children}
    </Title>
  </div>
);

const ListPhoto = ({ label, image, time }) => (
  <div
    style={{ display: "flex", flexDirection: "column", marginBottom: "14px" }}
  >
    <Text type="secondary">{label}</Text>
    {time === null ? (
      <Text>No Photo</Text>
    ) : (
      <div style={{ width: 300, height: 200, overflow: "hidden" }}>
        <Image width={300} src={image} />
      </div>
    )}
  </div>
);

const myDate = (date) => {
  return moment(date).format("YYYY-MM-DD");
};

const Attendance = () => {
  const { signOut } = useContext(AuthContext);
  const authContext = useContext(AuthContext);
  const [configData, setConfigData] = useState({
    page: 1,
    fromdate: myDate(authContext.firstDate),
    todate: myDate(authContext.lastDate),
    update: false,
  });
  const [perPage, setPerPage] = useState(20);
  const [empId, setEmpId] = useState("");

  const { data, pagination, loading } = useGetDataPagination(
    `attendance?emp_id=${empId}&from=${configData.fromdate}&to=${configData.todate}&page=${configData.page}&perpage=${perPage}`,
    configData.update
  );

  const { data: dataEmployee, loading: loadingEmployee } =
    useGetData("employee");

  const [date, setDate] = useState([
    myDate(authContext.firstDate),
    myDate(authContext.lastDate),
  ]);
  const [dataTemp, setDataTemp] = useState({});
  const [loadingExport, setLoadingExport] = useState(false);

  const [form] = Form.useForm();
  const { isOpen, onOpen, onClose } = useDisclosure();

  useEffect(() => {
    form.setFieldsValue({
      date: [moment(authContext.firstDate), moment(authContext.lastDate)],
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleFilterDate = () => {
    if (date.length === 0) {
      console.log("Form tidak bolah kosong");

      return null;
    }

    setConfigData({
      ...configData,
      page: 1,
      fromdate: date[0],
      todate: date[1],
    });
  };

  const ExportToExcel = async () => {
    setLoadingExport(true);
    try {
      const response = await axios.get(
        `${endpoint}/api/attendance/export?emp_id=${empId}&from=${configData.fromdate}&to=${configData.todate}`,
        {
          headers: {
            Authorization: `bearer ${authContext.userToken}`,
          },
          responseType: "blob",
        }
      );

      if (!response) {
        throw new Error();
      }

      message.success("Download in progress");
      fileDownload(response.data, "Attendance.xlsx");
      console.log(
        `fetch data /attendance/export?emp_id=${empId}&from=${configData.fromdate}&to=${configData.todate}`
      );
      setLoadingExport(false);
    } catch (err) {
      if (err.response?.status === 500) {
        message.error(`${err.response.status}, ${err.response.statusText}`);
        signOut();

        return null;
      }

      if (err.response) {
        message.error(`${err.response.status}, ${err.response.statusText}`);
        setLoadingExport(false);

        return null;
      }

      message.error("Something went wrong, please try again..");
      console.log(err);
      setLoadingExport(false);
    }
  };

  const columns = [
    {
      title: "#",
      dataIndex: "id",
      width: 70,
    },
    {
      title: "Name",
      dataIndex: "employe",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => {
            setDataTemp(record);
            onOpen();
          }}
        >
          {text}
        </Text>
      ),
      ellipsis: true,
    },
    {
      title: "Site",
      dataIndex: "site_name",
      responsive: ["lg"],
    },
    {
      title: "Date",
      dataIndex: "check_in",
      render: (text) => text?.substr(0, 10),
    },
    {
      title: "In",
      dataIndex: "check_in",
      render: (text) => text?.substr(11, 5),
    },
    {
      title: "Out",
      dataIndex: "check_out",
      render: (text) => (text === null ? "Not yet out" : text.substr(11, 5)),
    },
  ];
  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>Attendance</Breadcrumb.Item>
      </Breadcrumb>
      <Modal
        title={dataTemp.employe}
        visible={isOpen}
        onCancel={onClose}
        footer={false}
      >
        <FlexContainer style={{ marginBottom: "14px" }}>
          <div>
            <Text strong style={{ fontSize: 18 }}>
              {dataTemp.check_in?.substr(0, 10)}
            </Text>
          </div>
          <div>
            <Space>
              <Tag color={successColor}>
                <Text strong style={{ fontSize: 16, color: "white" }}>
                  IN {dataTemp.check_in?.substr(11, 5)}
                </Text>
              </Tag>
              <Tag color={dangerColor}>
                <Text strong style={{ fontSize: 16, color: "white" }}>
                  OUT{" "}
                  {dataTemp.check_out === null
                    ? "-"
                    : dataTemp.check_out?.substr(11, 5)}
                </Text>
              </Tag>
            </Space>
          </div>
        </FlexContainer>
        <ListPhoto label="Photo In" image={dataTemp.check_in_photo} />
        <ListContent label="Coordinate In">
          Lat: {dataTemp.check_in_lat}, Lng: {dataTemp.check_in_long}
        </ListContent>
        <ListPhoto
          label="Photo Out"
          time={dataTemp.check_out}
          image={dataTemp.check_out_photo}
        />
        <ListContent label="Coordinate Out">
          Lat: {dataTemp.check_out_lat}, Lng: {dataTemp.check_out_long}
        </ListContent>
      </Modal>
      <div>
        <Card>
          <Title level={4} type="secondary">
            Attendance
          </Title>
          <FlexContainer>
            <Form form={form} name="form-attendance" autoComplete="off">
              <div style={{ display: "flex", flexDirection: "row" }}>
                <FormRangePicker
                  name="date"
                  required
                  message="please select date"
                  onChange={(date, dateString) => setDate(dateString)}
                  format="YYYY-MM-DD"
                />
                <Form.Item>
                  <Button
                    type="primary"
                    shape="round"
                    icon={<FiFilter style={{ marginRight: "6px" }} />}
                    onClick={handleFilterDate}
                    loading={loading}
                    htmlType="submit"
                    style={{ marginLeft: 16 }}
                  >
                    Show
                  </Button>
                </Form.Item>
              </div>
            </Form>
            <FlexEnd style={{ marginBottom: 14 }}>
              <Button
                shape="round"
                icon={<RiFileExcel2Line style={{ marginRight: "6px" }} />}
                onClick={ExportToExcel}
                loading={loadingExport}
              >
                Export
              </Button>
            </FlexEnd>
          </FlexContainer>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <FormSelect
              name="empId"
              placeholder="filter employee"
              onChange={(e) => setEmpId(e)}
              loading={loadingEmployee}
              disabled={loadingEmployee}
              style={{ minWidth: 290 }}
            >
              {dataEmployee.map((item) => (
                <Option value={item.id} key={item.id}>
                  {`[${item.id}] ${item.name}`}
                </Option>
              ))}
            </FormSelect>
          </div>
          <Table
            columns={columns}
            dataSource={data}
            rowKey={(record) => record.id}
            loading={loading}
            scroll={{ y: 500 }}
            pagination={{
              total: pagination.total_data,
              showTotal: (total, range) =>
                `${range[0]}-${range[1]} of ${total} items`,
              showQuickJumper: true,
              defaultCurrent: 1,
              onChange: (pageNumber) =>
                setConfigData({
                  ...configData,
                  page: pageNumber,
                }),
              showSizeChanger: true,
              onShowSizeChange: (current, pageSize) => setPerPage(pageSize),
              defaultPageSize: perPage,
              pageSizeOptions: [20, 60, 100, 140],
            }}
          />
        </Card>
      </div>
    </div>
  );
};

export default withAuth(Attendance);
